import { useEffect, useState } from "react";
import { Elements, useTowerControl } from "../..";

export const useBetAmount = (callBack?: (value: number) => void) => {
  const { manager } = useTowerControl();
  const [betAmount, setBetAmt] = useState(manager.fields.general.amount);

  useEffect(() => {
    const removeListener = manager.listen({
      element: Elements.betAmount,
      callBack: (amt) => {
        if (callBack) callBack(amt);
        else setBetAmt(amt);
      },
    });
    return () => removeListener();
  }, []);

  const setBetAmount = (value: number) =>
    manager.setValue({
      element: Elements.betAmount,
      value,
    });

  return {
    betAmount,
    setBetAmount,
  };
};
