import React, { FC, useEffect, useMemo } from "react";

import { useSoundSystemControl } from "components";
import { useCoins, useLocalStorage } from "hooks";
import { useSeed } from "providers";

import Context from "./Context";
import { Elements, TowerManager } from "./manager";

export const TowerControlProvider: FC = ({ children }) => {
  const seed = useSeed();
  const [token] = useLocalStorage("token");
  const { controlFunctions, soundSystemState } = useSoundSystemControl();
  const { selectedCoin } = useCoins();

  const manager = useMemo(() => new TowerManager(seed), []);
  manager.seed = seed;

  useEffect(() => {
    manager.init();
    if (!token) {
      manager.setValue({
        element: Elements.autoCells,
        value: [],
      });

      manager.setValue({
        element: Elements.gameStatus,
        value: null,
      });

      manager.setValue({
        element: Elements.selectedRow,
        value: null,
      });

      manager.setValue({
        element: Elements.tableValue,
        value: [],
      });
    }

    return () => {
      manager.setValue({
        element: Elements.autoOpen,
        value: false,
      });
    };
  }, [Boolean(token)]);

  useEffect(() => {
    if (soundSystemState.isInteractivityWas) {
      manager.fields.general.audioControl = controlFunctions;
    }
  }, [soundSystemState.isInteractivityWas]);

  useEffect(() => {
    return () => {
      manager.emitter.removeAllListeners();
    };
  }, []);

  useEffect(() => {
    manager.setValue({
      element: Elements.autoStarted,
      value: false,
    });
  }, [selectedCoin?.coin]);

  return (
    <Context.Provider
      value={{
        manager,
      }}>
      {children}
    </Context.Provider>
  );
};
