import { useEffect } from "react";
import { track } from "utils";
import {
  MultipliersTypes,
  useSessionCoin,
  useTowerControl,
  useDiff,
  useBetAmount,
  useGameTable,
} from "../..";

const floor = 100000000;

export const useGetProfit = (rowNumber?: number) => {
  const { manager } = useTowerControl();
  const { diff } = useDiff();
  const { betAmount } = useBetAmount();
  const coin = useSessionCoin();
  const { selectedRow } = useGameTable();

  if (rowNumber === undefined) {
    rowNumber = Math.max((selectedRow || 0) - 1, 0);
  }

  try {
    const { config } = manager.fields;
    const { multipliers, limits } = config || {};
    if (multipliers && limits) {
      const currentLimits = limits[coin];

      const selectMultiplier = [
        ...multipliers[diff.toUpperCase() as keyof MultipliersTypes],
      ];

      return (
        Math.floor(
          Math.min(
            ((selectMultiplier[rowNumber] * 100) / 100) * betAmount - betAmount,
            currentLimits.maximum_bet,
          ) * floor,
        ) / floor
      ).toFixed(8);
    }
  } catch (error) {
    track()?.errors(error, {
      custom: {
        config: manager.fields,
        selectedRow: rowNumber,
        diff,
      },
      place: {
        fileName: "useGetProfit",
      },
    });
  }

  return (0).toFixed(8);
};
