import { useEffect, useState } from "react";
import { useTowerControl } from "../..";
import { Elements, Fields } from "../types";

export const useGameStatus = () => {
  const { manager } = useTowerControl();
  const [status, setStatus] = useState<Fields["gameStatus"]>(null);

  useEffect(() => {
    const removeListener = manager.listen({
      element: Elements.gameStatus,
      callBack: (status) => setStatus(status),
    });

    return () => removeListener();
  }, []);

  return { status };
};
