import { EventEmitter } from "events";
import { TowerManager } from ".";
import {
  CHANGE_AUTO_CELLS,
  CHANGE_AUTO_DECISION,
  CHANGE_AUTO_LIMITS,
  CHANGE_AUTO_OPEN,
  CHANGE_AUTO_STARTED,
  CHANGE_BET_AMOUNT,
  CHANGE_DIFF,
  CHANGE_GAME_STATUS,
  CHANGE_HISTORY,
  CHANGE_SESSION_COIN,
  CHANGE_TABLE_VALUE,
  TOWER_BET_LOCAL_NAME,
  TOWER_DIFF_LOCAL_NAME,
} from "./config";
import {
  AutoLimits,
  DecisionField,
  DecisionSetValue,
  Diff,
  Fields,
  GameTable,
  HistoryElement,
  TableListValues,
} from "./types";

export class TowersEmitter extends EventEmitter {
  manager: TowerManager;

  constructor(manager: TowerManager) {
    super();
    this.manager = manager;
  }

  //CHANGE_BET_AMOUNT
  setBetAmount(value: number) {
    this.manager.fields.general.amount = value;
    localStorage.setItem(TOWER_BET_LOCAL_NAME, `${value}`);
    this.emit(CHANGE_BET_AMOUNT, value);
  }
  betAmountListener(callBack: (value: number) => void) {
    this.addListener(CHANGE_BET_AMOUNT, callBack);

    return () => {
      this.removeListener(CHANGE_BET_AMOUNT, callBack);
    };
  }

  //CHANGE_DIFF
  setDiff(diff: Diff) {
    const { general } = this.manager.fields;
    general.diff = diff;
    localStorage.setItem(TOWER_DIFF_LOCAL_NAME, diff);

    this.emit(CHANGE_DIFF, diff);
  }
  diffListener(callBack: (diff: Diff) => void) {
    this.addListener(CHANGE_DIFF, callBack);

    return () => {
      this.removeListener(CHANGE_DIFF, callBack);
    };
  }

  //CHANGE_AUTO_OPEN
  setAutoOpen(value: boolean) {
    this.manager.fields.auto.sys.isOpen = value;
    this.emit(CHANGE_AUTO_OPEN, value);
  }
  autoOpenListener(callBack: (v: boolean) => void) {
    this.addListener(CHANGE_AUTO_OPEN, callBack);

    return () => {
      this.removeListener(CHANGE_AUTO_OPEN, callBack);
    };
  }

  //CHANGE_AUTO_STARTED
  setAutoStarted(value: boolean) {
    this.manager.fields.auto.sys.isStarted = value;
    this.emit(CHANGE_AUTO_STARTED, value);
  }
  autoStartedListener(callBack: (v: boolean) => void) {
    this.addListener(CHANGE_AUTO_STARTED, callBack);

    return () => {
      this.removeListener(CHANGE_AUTO_STARTED, callBack);
    };
  }

  //CHANGE_AUTO_DECISION
  setAutoDecision(value: DecisionSetValue) {
    const { betPercent, isWin, variant } = value;
    const { onLoss, onWin } = this.manager.fields.auto.fields.decision;

    const actualDecision = isWin ? onWin : onLoss;

    actualDecision.betPercent = betPercent;
    actualDecision.variant = variant;

    this.emit(CHANGE_AUTO_DECISION, {
      onLoss,
      onWin,
    });
  }
  decisionListener(callBack: (value: DecisionField) => void) {
    this.addListener(CHANGE_AUTO_DECISION, callBack);

    return () => {
      this.removeListener(CHANGE_AUTO_DECISION, callBack);
    };
  }

  //CHANGE_AUTO_LIMITS
  setAutoLimits(value: AutoLimits) {
    this.manager.fields.auto.fields.limits = value;
    this.emit(CHANGE_AUTO_LIMITS, { ...value });
  }
  limitsListener(callBack: (value: AutoLimits) => void) {
    this.addListener(CHANGE_AUTO_LIMITS, callBack);

    return () => {
      this.removeListener(CHANGE_AUTO_LIMITS, callBack);
    };
  }

  //CHANGE_GAME_STATUS
  setGameStatus(status: Fields["gameStatus"]) {
    this.manager.fields.gameStatus = status;
    this.emit(CHANGE_GAME_STATUS, status);
  }
  gameStatusListener(callBack: (status: Fields["gameStatus"]) => void) {
    this.addListener(CHANGE_GAME_STATUS, callBack);

    return () => {
      this.removeListener(CHANGE_GAME_STATUS, callBack);
    };
  }

  //CHANGE_TABLE_VALUE
  setTableValue(
    value:
      | {
          row: number;
          list: TableListValues[];
        }
      | TableListValues[][],
  ) {
    const { table } = this.manager.fields.general;
    if (Array.isArray(value)) {
      table.list = value;
    } else {
      const { list, row } = value;
      table.list[row] = list;
    }

    this.emit(CHANGE_TABLE_VALUE, table);
  }
  tableValueListener(callBack: (value: GameTable) => void) {
    this.addListener(CHANGE_TABLE_VALUE, callBack);

    return () => {
      this.removeListener(CHANGE_TABLE_VALUE, callBack);
    };
  }

  //CHANGE_SELECTED_ROW
  setSelectedRow(value: number | null) {
    const { table } = this.manager.fields.general;
    table.selectedRow = value;
    this.emit(CHANGE_TABLE_VALUE, table);
  }
  selectedRowListener(callBack: (value: GameTable) => void) {
    this.addListener(CHANGE_TABLE_VALUE, callBack);

    return () => {
      this.removeListener(CHANGE_TABLE_VALUE, callBack);
    };
  }

  //CHANGE_SESSION_COIN
  setSessionCoin(value?: string) {
    const { general } = this.manager.fields;
    general.roundCoin = value || null;
    this.emit(CHANGE_SESSION_COIN, value);
  }
  sessionCoinListener(callBack: (value: string) => void) {
    this.addListener(CHANGE_SESSION_COIN, callBack);

    return () => {
      this.removeListener(CHANGE_SESSION_COIN, callBack);
    };
  }

  //CHANGE_HISTORY
  setHistory(value: HistoryElement[] | HistoryElement) {
    const { general } = this.manager.fields;
    if (Array.isArray(value)) {
      general.history = value;
    } else {
      general.history.push(value);
    }

    const { history } = general;
    const length = history.length;

    if (length > 10) {
      general.history = history.slice(length - 10, length);
    }

    this.emit(CHANGE_HISTORY, [...general.history]);
  }
  historyListener(callBack: (value: HistoryElement[]) => void) {
    this.addListener(CHANGE_HISTORY, callBack);

    return () => {
      this.removeListener(CHANGE_HISTORY, callBack);
    };
  }

  //CHANGE_AUTO_CELLS
  setAutoCells(
    values:
      | number[]
      | {
          row: number;
          cell: number;
        },
  ) {
    const { table } = this.manager.fields.auto;
    if (Array.isArray(values)) {
      table.selectedCells = values;
    } else {
      const { cell, row } = values;
      if (table.selectedCells[row] !== cell) {
        table.selectedCells[row] = cell;
      } else {
        //@ts-ignore
        table.selectedCells[row] = undefined;
      }
    }
    this.emit(CHANGE_AUTO_CELLS, table.selectedCells);
  }
  autoCellsListener(callBack: (values: number[]) => void) {
    this.addListener(CHANGE_AUTO_CELLS, callBack);

    return () => {
      this.removeListener(CHANGE_AUTO_CELLS, callBack);
    };
  }
}
