import { useEffect, useState } from "react";
import { Elements, useTowerControl } from "../..";

export const useAutoCells = () => {
  const { manager } = useTowerControl();
  const [cells, setCells] = useState<number[]>([]);

  useEffect(() => {
    const removeListener = manager.listen({
      element: Elements.autoCells,
      callBack: (cells) => setCells([...cells]),
    });

    return () => {
      removeListener();
    };
  }, []);

  const setAutoCells = (value: { row: number; cell: number }) =>
    manager.setValue({
      element: Elements.autoCells,
      value,
    });

  return {
    cells,
    setCells: setAutoCells,
  };
};
