import { useLayoutEffect, useState } from "react";
import { Elements, useTowerControl, Diff } from "../..";

export const useDiff = () => {
  const { manager } = useTowerControl();
  const [diff, setDiff] = useState<Diff>(manager.fields.general.diff);

  useLayoutEffect(() => {
    const removeListener = manager.listen({
      element: Elements.diff,
      callBack: (diff) => setDiff(diff),
    });

    return () => {
      removeListener();
    };
  }, []);

  const handleChangeDiff = (value: Diff) => {
    manager.setValue({
      element: Elements.diff,
      value,
    });
  };

  return {
    diff,
    setDiff: handleChangeDiff,
  };
};
