import { useEffect, useState } from "react";
import { Elements, useTowerControl, GameTable } from "../..";

export const useGameTable = () => {
  const { manager } = useTowerControl();
  const [table, setTable] = useState<GameTable>({
    list: [],
    selectedRow: null,
  });

  useEffect(() => {
    const removeListener = manager.listen({
      element: Elements.tableValue,
      callBack: (table) => setTable({ ...table }),
    });

    return () => {
      removeListener();
    };
  }, []);

  return table;
};
