import { useCoins } from "hooks";
import { useEffect, useState } from "react";
import { useTowerControl, Elements } from "../..";

export const useSessionCoin = () => {
  const { manager } = useTowerControl();
  const { selectedCoin } = useCoins();
  const [coin, setCoin] = useState<string | null>(null);

  useEffect(() => {
    const unListen = manager.listen({
      element: Elements.sessionCoin,
      callBack: (coin) => {
        setCoin(coin);
      },
    });
    return () => {
      unListen();
    };
  }, []);

  return coin || selectedCoin?.coin || "GEM";
};
