import { useEffect, useState } from "react";
import { Elements, useTowerControl } from "../..";

export const useIsAutoOpen = () => {
  const { manager } = useTowerControl();
  const [isOpen, setIsOpen] = useState(manager.fields.auto.sys.isOpen);

  useEffect(() => {
    const unListen = manager.listen({
      element: Elements.autoOpen,
      callBack: (value) => {
        setIsOpen(value);
      },
    });

    return () => {
      unListen();
    };
  }, []);

  return {
    isOpen,
    setIsOpen: (value: boolean) => {
      manager.setValue({
        element: Elements.autoOpen,
        value,
      });
    },
  };
};
