import { useEffect, useState } from "react";
import {
  Elements,
  useTowerControl,
  TowerManager,
  DecisionSetValue,
} from "../..";

const getDefaultAutoDecision = (manager: TowerManager) => {
  const { onLoss, onWin } = manager.fields.auto.fields.decision;

  return {
    win: {
      betPercent: onWin.betPercent,
      variant: onWin.variant,
    },
    loss: {
      betPercent: onLoss.betPercent,
      variant: onLoss.variant,
    },
  };
};

export const useDecision = () => {
  const { manager } = useTowerControl();

  const [decision, setDecision] = useState(getDefaultAutoDecision(manager));

  useEffect(() => {
    const removeListener = manager.listen({
      element: Elements.autoDecision,
      callBack: ({ onLoss, onWin }) => {
        setDecision({
          win: {
            ...onWin,
          },
          loss: {
            ...onLoss,
          },
        });
      },
    });
    return () => {
      removeListener();
    };
  }, []);

  const handleChangeDecision = (value: DecisionSetValue) =>
    manager.setValue({ element: Elements.autoDecision, value });

  return {
    decision,
    setDecision: handleChangeDecision,
  };
};
