import { fetchBase, rangeBetween, repeat, sleep, track } from "utils";
import { controlFunctionsType, liveStatsManager } from "components";
import { SeedContextType } from "providers";
import { coinsManager } from "hooks";

import notifybarManager from "components/NotifyBar/notifybar-manager";
import {
  CHANGE_AUTO_LIMITS,
  TOWER_BET_LOCAL_NAME,
  TOWER_DIFF_LOCAL_NAME,
} from "./config";
import { TowersEmitter } from "./emitter";
import {
  AutoLimits,
  Diff,
  Elements,
  FetchCashout,
  FetchLimits,
  FetchPlay,
  FetchSession,
  FetchSweep,
  Fields,
  GameTable,
  Limits,
  ListenerType,
  Multipliers,
  MultipliersTypes,
  PlayResult,
  PlayTableMatrix,
  SetValueType,
  TableListValues,
} from "./types";

export * from "./types";
export * from "./hooks";

export class TowerManager {
  emitter: TowersEmitter;
  fields: Fields = {
    general: {
      amount: 0.01,
      diff: "medium",
      roundCoin: null,

      table: {
        list: [],
        selectedRow: null,
      },
      history: [],
    },
    auto: {
      fields: {
        decision: {
          onLoss: {
            betPercent: 100,
            variant: 1,
          },
          onWin: {
            betPercent: 0,
            variant: 1,
          },
        },
        limits: {
          numberOfBets: 0,
          stopOnLoss: 0,
          stopOnProfit: 0,
        },
        limitsData: {
          numberOfBets: 0,
          profit: 0,
        },
      },
      sys: {
        isOpen: false,
        isStarted: false,

        baseBet: 0,
      },
      table: {
        selectedCells: [],
      },
    },
    cellsAmount: {
      easy: 4,
      medium: 3,
      hard: 2,
      brutal: 4,
      wicked: 3,
    },

    config: undefined,
    gameStatus: null,
  };
  seed: SeedContextType;

  constructor(seed: SeedContextType) {
    const { general } = this.fields;

    try {
      const diffLocalValue = localStorage.getItem(
        TOWER_DIFF_LOCAL_NAME,
      ) as Diff | null;
      general.diff = diffLocalValue || "medium";
    } catch {}

    this.emitter = new TowersEmitter(this);
    this.seed = seed;
  }

  async init() {
    const { data } = await fetchBase<FetchLimits>("towers/get-configuration");

    if (data) {
      const { coins, multipliers } = data;
      const newLimits = coins.reduce<Record<string, Limits>>(
        (acm, { coin, maximum_bet, maximum_profit, minimum_bet }) => {
          acm[coin] = {
            coin,
            maximum_bet: Number(maximum_bet),
            maximum_profit: Number(maximum_profit),
            minimum_bet: Number(minimum_bet),
          };
          return acm;
        },
        {},
      );

      const round = 100,
        fixed = 100000;
      const calcMultipliers = Object.entries(multipliers).reduce((acm, mul) => {
        const [key, numbers] = mul as [keyof MultipliersTypes, number[]];
        acm[key] = numbers.reverse().reduce(
          (acm, number) => {
            acm.multi =
              Math.floor(((number * round) / round) * acm.multi * fixed) /
              fixed;
            acm.list.push(acm.multi);
            return acm;
          },
          {
            multi: 1,
            list: [],
          } as {
            multi: number;
            list: number[];
          },
        ).list as Multipliers;

        return acm;
      }, {} as MultipliersTypes);

      this.fields.config = {
        limits: newLimits,
        multipliers: calcMultipliers,
      };

      const { coin = "GEM" } = coinsManager.selectedCoin || {};

      const defaultBetValue = rangeBetween(
        Number(localStorage.getItem(TOWER_BET_LOCAL_NAME) || 0),
        newLimits[coin].minimum_bet,
        Math.min(
          Number(coinsManager.selectedCoin?.balance || 0),
          newLimits[coin].maximum_bet,
        ),
      );

      this.setValue({ element: Elements.betAmount, value: defaultBetValue });
    }

    await this.getSession();

    this.listen({
      element: Elements.autoStarted,
      callBack: (is) => {
        if (is) {
          this.playAuto();
        }
      },
    });
  }

  error(err: string | any, method?: string) {
    const { auto } = this.fields;
    const { isStarted } = auto.sys;
    notifybarManager.error({
      message: err,
      timeOut: isStarted ? Infinity : 5000,
    });

    track()?.errors(err, {
      custom: {
        game: "Towers",
        gameFields: this.fields,
        method,
      },
      place: {
        fileName: "Towers/controller/index.tx",
      },
    });

    if (isStarted) {
      this.setValue({
        element: Elements.autoStarted,
        value: false,
      });
    }
  }

  setValue(value: SetValueType) {
    const { auto, general } = this.fields;
    const setMap: Record<Elements, keyof TowersEmitter> = {
      autoDecision: "setAutoDecision",
      sessionCoin: "setSessionCoin",
      selectedRow: "setSelectedRow",
      tableValue: "setTableValue",
      autoCells: "setAutoCells",
      betAmount: "setBetAmount",
      limits: "setAutoLimits",
      history: "setHistory",

      diff: "setDiff",
      autoOpen: "setAutoOpen",
      gameStatus: "setGameStatus",
      autoStarted: "setAutoStarted",
    };

    switch (value.element) {
      case "gameStatus":
        this.emitter.setGameStatus(value.value);
        if (
          value.value === "IN_PROGRESS" &&
          auto.sys.isOpen &&
          !auto.sys.isStarted
        ) {
          this.emitter.setAutoOpen(false);
        }
        break;

      case "diff":
        this.emitter.setDiff(value.value);
        this.emitter.setTableValue([]);
        this.emitter.setAutoCells([]);
        break;

      case "autoOpen":
        this.emitter.setAutoStarted(false);
        this.emitter.setTableValue([]);
        this.emitter.setAutoOpen(value.value);
        break;

      case "autoStarted":
        if (value.value) {
          //if auto turn on
          auto.sys.baseBet = general.amount;
        } else if (auto.sys.baseBet) {
          //if auto turn off
          console.log(this.fields);

          this.emitter.setBetAmount(auto.sys.baseBet);

          if (this.fields.gameStatus === "IN_PROGRESS") {
            this.emitter.setAutoOpen(false);
          }
        }
        auto.fields.limitsData.numberOfBets = 0;
        auto.fields.limitsData.profit = 0;

        this.emitter.setSessionCoin();
        this.emitter.setAutoLimits(auto.fields.limits);
        this.emitter.setAutoStarted(value.value);
        break;

      default:
        (this.emitter[setMap[value.element]] as (value: any) => void)(
          value.value,
        );
        break;
    }
  }

  listen(value: ListenerType) {
    const listenMap: Record<Elements, keyof TowersEmitter> = {
      betAmount: "betAmountListener",
      diff: "diffListener",
      autoOpen: "autoOpenListener",
      autoStarted: "autoStartedListener",
      autoDecision: "decisionListener",
      limits: "limitsListener",
      gameStatus: "gameStatusListener",
      tableValue: "tableValueListener",
      selectedRow: "selectedRowListener",
      sessionCoin: "sessionCoinListener",
      history: "historyListener",
      autoCells: "autoCellsListener",
    };

    return (
      this.emitter[listenMap[value.element]] as (value: any) => () => void
    )(value.callBack);
  }

  reduceMatrix(matrix: PlayTableMatrix[]) {
    try {
      return matrix.reduce(
        (
          acm,
          { bad_locations, good_locations, current_multiplier, return_amount },
          i,
        ) => {
          const goodList: TableListValues[] = good_locations.map((value) => ({
            value,
            status: "good",
          }));

          const badList: TableListValues[] = bad_locations.map((value) => ({
            value,
            status: "bad",
          }));

          acm.list[i] = [...goodList, ...badList];
          if (return_amount) acm.amount.push(return_amount);
          if (current_multiplier) acm.multipliers.push(current_multiplier);
          return acm;
        },
        {
          list: [],
          multipliers: [],
          amount: [],
        } as {
          list: GameTable["list"];
          multipliers: number[];
          amount: string[];
        },
      );
    } catch (error) {
      console.log("matrix>> ", matrix);

      track()?.errors(error, {
        custom: {
          matrix,
        },
        place: {
          fileName: "Tower/manager",
          lineNumber: 324,
        },
      });

      return {
        list: [],
        multipliers: [],
        amount: [],
      };
    }
  }

  reduceResult(results: PlayResult[]) {
    try {
      return results.reduce(
        (acm, { attempted, reward_locations, swept_location }, i) => {
          if (swept_location) {
            acm.selectedRow = i + 1;
          }

          if (reward_locations) {
            acm.list[i] = reward_locations.map((value) => ({
              value,
              selected: swept_location === value,
              status: "good",
            }));

            if (reward_locations.length) {
              const { cellsAmount, general } = this.fields;
              const actualCellsAmount = cellsAmount[general.diff];
              const badLocations: TableListValues[] = repeat(
                actualCellsAmount,
                (i) => i,
              )
                .filter(
                  (badValue) =>
                    !acm.list[i].find(({ value }) => {
                      return badValue === value;
                    }),
                )
                .map((value) => ({
                  value,
                  selected: false,
                  status: "bad",
                }));

              acm.list[i].push(...badLocations);
            }
          }

          return acm;
        },
        {
          list: [],
          selectedRow: null,
        } as GameTable,
      );
    } catch {
      console.log("Result >>", results);

      return {
        list: [],
        selectedRow: null,
      };
    }
  }

  async getSession() {
    const { data, error } = await fetchBase<FetchSession>("towers/session", {
      stopIfNoToken: true,
    });
    if (data?.session === null) {
      this.setValue({
        element: Elements.gameStatus,
        value: "NO_PLAY",
      });
    } else if (data?.session) {
      const { difficulty, coin } = data?.session;

      this.setValue({
        element: Elements.sessionCoin,
        value: coin,
      });
      this.setValue({
        element: Elements.diff,
        value: difficulty.toLowerCase() as Diff,
      });

      this.setValue({
        element: Elements.gameStatus,
        value: "IN_PROGRESS",
      });

      const { list, selectedRow } = this.reduceResult(data.session.results);

      this.fields.general.table.selectedRow =
        selectedRow || data.session.current_row
          ? Number(data.session.current_row)
          : null;

      this.setValue({
        element: Elements.tableValue,
        value: list,
      });
    } else if (error) this.error(error, "getSession");
  }

  async play() {
    const { amount, diff } = this.fields.general;
    const { coin = "GEM" } = coinsManager.selectedCoin || {};
    const { clientSeed } = this.seed;

    this.setValue({
      element: Elements.sessionCoin,
      value: coin,
    });

    const { data, error } = await fetchBase<FetchPlay>("towers/play", {
      variables: {
        bet_amt: amount.toFixed(8),
        coin,
        client_seed: clientSeed?.seed || "seed",
        difficulty: diff.toUpperCase(),
      },
      stopIfNoToken: true,
    });

    if (data?.success) {
      const { list, selectedRow } = this.reduceResult(data.results);
      this.fields.general.table.selectedRow = selectedRow;
      this.setValue({
        element: Elements.tableValue,
        value: list,
      });
      this.setValue({
        element: Elements.gameStatus,
        value: "IN_PROGRESS",
      });
    }

    if (error) this.error(error, "play");
  }

  async sweep(location: number) {
    const { auto, general } = this.fields;

    const { data, error } = await fetchBase<FetchSweep>("towers/sweep", {
      variables: {
        location,
      },
    });

    if (data?.success) {
      switch (data.status) {
        case "BUSTED":
          const { balance, bet_id, game_table_matrix, profit, results } = data;
          const resultProfit = Number(profit);
          auto.fields.limitsData.profit += resultProfit;
          const playCoin =
            general.roundCoin || coinsManager.selectedCoin?.coin || "GEM";
          coinsManager.coinValueUpdate(balance, playCoin);
          general.audioControl?.playEffect("loss");

          liveStatsManager.addBet({
            amount: general.amount,
            coin: playCoin,
            profit: resultProfit,
          });

          const result = this.reduceResult(results);
          const matrix = this.reduceMatrix(game_table_matrix);
          general.table.selectedRow = result.selectedRow;

          this.setValue({
            element: Elements.tableValue,
            value: matrix.list,
          });
          this.setValue({
            element: Elements.gameStatus,
            value: "BUSTED",
          });
          this.setValue({
            element: Elements.history,
            value: {
              id: bet_id,
              value: 0,
            },
          });
          break;

        case "IN_PROGRESS":
          const { table } = this.fields.general;
          table.selectedRow = data.current_row;

          const progressMatrix = this.reduceMatrix([data.previous_row]);
          const newProgress = [...table.list, ...progressMatrix.list].filter(
            (value) => value.length,
          );
          this.setValue({
            element: Elements.tableValue,
            value: newProgress,
          });
          break;

        case "CASHED_OUT":
          this.processCashout(data);
          break;

        default:
          break;
      }
    }

    if (error) this.error(error, "sweep");

    return data?.status;
  }

  async cashout() {
    const { data, error } = await fetchBase<FetchCashout>("towers/cashout");

    if (data?.success) {
      this.processCashout(data);
    }

    if (error) this.error(error, "cashout");
  }

  processCashout(data: FetchCashout) {
    const {
      balance,
      current_multiplier,
      game_table_matrix,
      profit,
      bet_id,
      results,
      status,
    } = data;

    const result = this.reduceResult(results);
    const matrix = this.reduceMatrix(game_table_matrix);

    const { general, auto } = this.fields;
    const resultProfit = Number(profit);
    auto.fields.limitsData.profit += resultProfit;
    general.table.selectedRow = result.selectedRow;

    const playCoin = general.roundCoin || coinsManager.selectedCoin?.coin || "";
    coinsManager.coinValueUpdate(balance, playCoin);

    liveStatsManager.addBet({
      amount: general.amount,
      coin: playCoin,
      profit: resultProfit,
    });

    this.setValue({
      element: Elements.gameStatus,
      value: status,
    });
    this.setValue({
      element: Elements.tableValue,
      value: matrix.list,
    });
    this.setValue({
      element: Elements.sessionCoin,
    });
    this.setValue({
      element: Elements.history,
      value: {
        id: bet_id,
        value: current_multiplier,
      },
    });

    general.audioControl?.playEffect("win");
  }

  async onClick() {
    const { gameStatus, auto, general } = this.fields;
    if (auto.sys.isOpen) {
      this.setValue({
        element: Elements.autoStarted,
        value: !auto.sys.isStarted,
      });
    } else {
      switch (gameStatus) {
        case "NO_PLAY":
        case "BUSTED":
        case "CASHED_OUT":
          general.audioControl?.playEffect("bet");
          this.play();
          break;

        case "IN_PROGRESS":
          general.audioControl?.playEffect("cashout");
          this.cashout();
          break;

        default:
          break;
      }
    }
  }

  async playAuto() {
    if (!this.fields.auto.sys.isStarted || !this.fields.config) return;

    await this.play();

    const { auto, general, config } = this.fields;
    const { fields, sys } = auto;
    const { onLoss, onWin } = fields.decision;
    fields.limitsData.numberOfBets++;

    let isOnProgress = false;
    let selectedDecision = onLoss;

    for await (let location of auto.table.selectedCells) {
      await sleep(0.15);

      if (!auto.sys.isStarted) {
        return;
      }

      if (location === undefined) break;

      const status = await this.sweep(location);

      switch (status) {
        case "IN_PROGRESS":
          isOnProgress = true;
          break;

        //@ts-ignore -- Fallthrough
        case "CASHED_OUT":
          selectedDecision = onWin;
        //@ts-ignore -- Fallthrough
        default:
          isOnProgress = false;
          break;
      }

      if (!isOnProgress) break;
    }

    if (isOnProgress) {
      await this.cashout();
      selectedDecision = onWin;
    }

    const { betPercent, variant } = selectedDecision;
    const { amount, roundCoin } = general;
    const { limits } = config;

    const activeCoin = roundCoin || coinsManager.selectedCoin?.coin || "GEM";
    const activeLimits = limits[activeCoin];

    const stopAuto = () =>
      this.setValue({
        element: Elements.autoStarted,
        value: false,
      });

    switch (variant) {
      case 0:
        const betCalculation = rangeBetween(
          amount + (amount * betPercent) / 100,
          activeLimits.minimum_bet,
          activeLimits.maximum_bet,
        );

        this.setValue({
          element: Elements.betAmount,
          value: betCalculation,
        });
        break;

      case 1:
        this.setValue({
          element: Elements.betAmount,
          value: sys.baseBet,
        });
        break;

      case 2:
        stopAuto();
        return;

      default:
        break;
    }

    const getResult = (key: keyof AutoLimits, value: number) => {
      const { numberOfBets, profit } = fields.limitsData;

      switch (key) {
        case "numberOfBets":
          return value - numberOfBets;

        case "stopOnLoss":
          return value - profit * -1;

        case "stopOnProfit":
          return value - profit;
        default:
          return 0;
      }
    };

    for (const key in fields.limits) {
      const limit = key as keyof AutoLimits;
      const value = fields.limits[limit];

      if (value) {
        const result = getResult(limit, value);
        if (result <= 0) {
          stopAuto();
          return;
        }

        if (limit === "numberOfBets")
          this.emitter.emit(CHANGE_AUTO_LIMITS, {
            ...fields.limits,
            numberOfBets: result,
          });
      }
    }

    await sleep(1 / 4); //four runs per second

    this.playAuto();
  }
}

export * from "./config";
