import { useEffect, useState } from "react";
import { Elements, useTowerControl } from "../..";

export const useIsAutoStarted = () => {
  const { manager } = useTowerControl();
  const [isStarted, setIsStarted] = useState(manager.fields.auto.sys.isStarted);

  useEffect(() => {
    const removeListener = manager.listen({
      element: Elements.autoStarted,
      callBack: (value) => {
        setIsStarted(value);
      },
    });

    return () => {
      removeListener();
    };
  }, []);

  return {
    isStarted,
    setIsOpen: (value: boolean) => {
      manager.setValue({
        element: Elements.autoStarted,
        value,
      });
    },
  };
};
