import { controlFunctionsType } from "components";

export type Diff = "easy" | "medium" | "hard" | "wicked" | "brutal";

export interface Decision {
  variant: number;
  betPercent: number;
}

export interface DecisionSetValue extends Decision {
  isWin: boolean;
}

export interface DecisionField {
  onWin: Decision;
  onLoss: Decision;
}

export interface TableListValues {
  value: number;
  selected?: boolean;
  status?: "good" | "bad";
}

export interface HistoryElement {
  value: string | number;
  id: string;
}

export interface GameTable {
  list: TableListValues[][];
  selectedRow: number | null;
}
export interface Fields {
  general: {
    amount: number;
    diff: Diff;

    table: GameTable;
    roundCoin: string | null;
    history: HistoryElement[];
    audioControl?: controlFunctionsType;
  };
  auto: {
    sys: {
      isOpen: boolean;
      isStarted: boolean;

      baseBet: number;
    };
    fields: {
      decision: DecisionField;
      limits: AutoLimits;
      limitsData: {
        profit: number;
        numberOfBets: number;
      };
    };
    table: {
      selectedCells: (number | undefined)[];
    };
  };
  config?: {
    limits: Record<string, Limits>;
    multipliers: MultipliersTypes;
  };
  cellsAmount: Record<Diff, number>;
  gameStatus: "NO_PLAY" | "IN_PROGRESS" | "BUSTED" | "CASHED_OUT" | null;
}

export type Multipliers = [
  number,
  number,
  number,
  number,
  number,
  number,
  number,
  number,
];

export interface Limits {
  coin: string;
  maximum_bet: number;
  minimum_bet: number;
  maximum_profit: number;
}

export interface MultipliersTypes {
  WICKED: Multipliers;
  BRUTAL: Multipliers;
  HARD: Multipliers;
  MEDIUM: Multipliers;
  EASY: Multipliers;
}

export interface FetchLimits {
  coins: {
    coin: string;
    maximum_bet: string;
    maximum_profit: string;
    minimum_bet: string;
  }[];

  multipliers: MultipliersTypes;
}

export interface AutoLimits {
  numberOfBets: number;
  stopOnProfit: number;
  stopOnLoss: number;
}

export enum Elements {
  betAmount = "betAmount",
  diff = "diff",
  autoOpen = "autoOpen",
  autoStarted = "autoStarted",
  autoDecision = "autoDecision",
  limits = "limits",
  gameStatus = "gameStatus",
  tableValue = "tableValue",
  selectedRow = "selectedRow",
  sessionCoin = "sessionCoin",
  history = "history",
  autoCells = "autoCells",
}

export type SetValueType =
  | {
      element: Elements.betAmount;
      value: number;
    }
  | {
      element: Elements.diff;
      value: Diff;
    }
  | {
      element: Elements.autoOpen | Elements.autoStarted;
      value: boolean;
    }
  | {
      element: Elements.autoDecision;
      value: DecisionSetValue;
    }
  | {
      element: Elements.limits;
      value: AutoLimits;
    }
  | {
      element: Elements.gameStatus;
      value: Fields["gameStatus"];
    }
  | {
      element: Elements.tableValue;
      value:
        | {
            row: number;
            list: TableListValues[];
          }
        | TableListValues[][];
    }
  | {
      element: Elements.selectedRow;
      value: number | null;
    }
  | {
      element: Elements.sessionCoin;
      value?: string;
    }
  | {
      element: Elements.history;
      value: HistoryElement[] | HistoryElement;
    }
  | {
      element: Elements.autoCells;
      value:
        | number[]
        | {
            row: number;
            cell: number;
          };
    };

export type ListenerType =
  | {
      element: Elements.betAmount;
      callBack: (value: number) => void;
    }
  | {
      element: Elements.diff;
      callBack: (value: Diff) => void;
    }
  | {
      element: Elements.autoOpen | Elements.autoStarted;
      callBack: (value: boolean) => void;
    }
  | {
      element: Elements.autoDecision;
      callBack: (value: DecisionField) => void;
    }
  | {
      element: Elements.limits;
      callBack: (value: AutoLimits) => void;
    }
  | {
      element: Elements.gameStatus;
      callBack: (value: Fields["gameStatus"]) => void;
    }
  | {
      element: Elements.tableValue;
      callBack: (value: GameTable) => void;
    }
  | {
      element: Elements.selectedRow;
      callBack: (value: GameTable) => void;
    }
  | {
      element: Elements.sessionCoin;
      callBack: (value: string | null) => void;
    }
  | {
      element: Elements.history;
      callBack: (value: HistoryElement[]) => void;
    }
  | {
      element: Elements.autoCells;
      callBack: (value: number[]) => void;
    };

export interface PlayResult {
  attempted: boolean;
  reward_locations: number[];
  swept_location: number | null;
}
export interface PlayTableMatrix {
  current_multiplier?: number;
  good_locations: number[];
  bad_locations: number[];
  return_amount?: string;
}
interface ActiveSession {
  bet_amt: string;
  client_seed: string;
  coin: string;
  current_row: string;
  difficulty: Diff;
  results: PlayResult[];
}
export interface FetchSession {
  session: null | ActiveSession;
}

export interface FetchPlay {
  balance: string;
  message: string;
  number_of_columns: number;
  number_of_rows: number;
  results: PlayResult[];
  success: boolean;
}

export type FetchSweep =
  | {
      message: string;
      success: boolean;

      current_row: number;
      previous_row: { good_locations: number[]; bad_locations: number[] };
      status: "IN_PROGRESS";
    }
  | {
      message: string;
      success: boolean;

      balance: string;
      bet_id: string;
      game_table_matrix: PlayTableMatrix[];
      profit: string;
      results: PlayResult[];
      status: "BUSTED";
    }
  | FetchCashout;

export interface FetchCashout {
  balance: string;
  bet_id: string;
  current_multiplier: string;
  current_row: string;
  game_table_matrix: PlayTableMatrix[];
  message: string;
  profit: string;
  results: [
    { attempted: boolean; reward_locations: number[]; swept_location: number },
  ];
  status: "CASHED_OUT";
  success: boolean;
}
